<template>
  <div class="post-item">
    <Panel>
      <template #main>
        <div class="post-header"></div>
        <div v-if="post" class="post-main" v-html="post.content"></div>
        <el-skeleton v-else :rows="6" animated />
      </template>
    </Panel>
  </div>
</template>

<script>
import Panel from "../../containers/Panel.vue";
export default {
  components: { Panel },
  // data() {
  //   return {
  //     post this.$store.state.post,
  //   };
  // },
  computed: {
    post() {
      return this.$store.state.Post.post;
    },
  },
  watch: {
    "$route.params.id": {
      handler(newVal) {
        this.$store.dispatch("getId", newVal);
        // console.log(newVal);
      },
      immediate: true,
    },
  },
};
</script>

